

/* Center a div */
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
/* Create a Card widget */

.list {
  text-decoration: none;
  list-style-type: none;
  color: black;
  font-weight: bold;
  width: 50%;
  color: white;
}
.bg {
  background-color: rgba(0, 0, 0, 0.788);
  text-decoration: none;
  list-style-type: none;
  color: black;
  font-weight: bold;
  font-size: 20px;
  padding: 20px;
  width: 50vw;
  margin-left: 10px;
  border-radius: 10px;
  color: white;
}
.cart{
  background-color: rgba(255, 255, 255, 0.849);
  text-decoration: none;
  list-style-type: none;
  color: rgb(0, 0, 0);
  font-weight: bold;
  font-size: 20px;
  padding: 20px;
  width: 50vw;
  margin-left: 10px;
  border-radius: 10px;
}